#visual-console-container {
  margin: 0px auto;
  position: relative;
  background-repeat: no-repeat;
  background-size: 100% 100%;
  background-position: center;
}

.visual-console-item {
  position: absolute;
  display: flex;
  flex-direction: initial;
  justify-items: center;
  align-items: center;
  user-select: text;
}
